<template>
  <div class="personal-content-body">
    <div class="content folder-content">
      <div class="content-title clear">
        <div class="left">{{typename}}文件总览</div>
      </div>
      <div class="historyapply-group folder-historyapply-group">
        <div class="historyapply-group-top clear flex flex_left">
          <div class="left breadbox">
            <span class="breadicon left">
              <i class="el-icon-d-arrow-left"></i>
            </span>
            <router-link class="breadtext left" :to="{ name: 'directInvestProjectRunProjectFile' ,query: query}">返回项目文件</router-link>
          </div>
          <div class="historyapply-group-top-title left">共包含{{total}}个{{typename}}</div>
          <div class="historyapply-group-top-dropdown select-normal left">
            <el-select v-model="projectId" filterable placeholder="选择项目" @change="changeProject($event)">
              <el-option v-for="item in projectList" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="foldermanage-content">
          <el-tree :data="fileInfo" node-key="id" default-expand-all :expand-on-click-node="false" :props="shareProp" :highlight-current="true" class="filetree">
            <div class="custom-tree-node" slot-scope="{ node, data }">
              <div class="node-title">
                <img class="node-icon" :src="node.data.imgurl">
                <em v-if="node.data.type=='folder'">{{node.data.dirname}}</em>
                <em v-if="node.data.type=='doc'">{{node.data.doc_title}}</em>
              </div>
            </div>
          </el-tree>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { dirTree, allProjectOrFund } from '../../config/api'
import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
export default {
  data() {
    return {
      type: 1, //1项目 2基金
      projectList: '', //项目/基金列表
      total: '', //项目/基金总数
      typename: '项目', //1项目 2基金
      flag: 1, //1=>含子目录和文件2=>只包含子目录
      projectId: -1, //项目id，-1->为选择项目
      fileInfo: [], //树结构数据
      query: {},
      shareProp: {
        children: 'contents'
      },
    }
  },
  created() {
    if (this.$route.query.type == undefined) {
      this.$router.go(-1);
    } else {
      this.type = this.$route.query.type;
      if (this.type == 1) {
        this.typename = '项目'
      } else {
        this.typename = '基金'
      }
      let query = {};
      for (let i in this.$route.query) {
        if (i != 'type') {
          query[i] = this.$route.query[i]
        }
      }
      this.query = query;
      this.getProjectList();
      this.initData();
    }
  },
  computed: {},
  mounted() {},
  methods: {
    initData() {
      let param = {
        type: this.type,
        flag: this.flag, //
      }
      if (this.projectId == -1) {
        param.id = '';
      } else {
        param.id = this.projectId
      }
      dirTree(param).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          this.xunhuan(data);
          this.fileInfo = data;
        }
      });
    },
    //获取项目列表
    getProjectList() {
      //获取项目列表
      allProjectOrFund({
        type: this.type
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.projectList = res.data.payload.data;
          if (this.projectList.length == 0) {} else {
            this.total = res.data.payload.total;
            this.projectList.unshift({
              id: -1,
              name: '全部'
            })
          }
        }
      });
    },
    //修改项目
    changeProject(e) {
      this.projectId = e;
      this.initData();
    },
    //循环处理数据
    xunhuan(data) {
      data.forEach((item, index) => {
        if (item.dirname != undefined) {
          item.type = 'folder';
          item.imgurl = '../../../static/images/fileicon_folder.png'
        } else {
          item.type = 'doc';
          let typestring = this.getFileType(item.doc_title);
          let imgurl = '../../../static/images/fileicon_' + typestring + '.png';
          item.imgurl = imgurl;
        }
        if (item.contents != undefined) {
          if (item.contents.length > 0) {
            this.xunhuan(item.contents);
          }
        }
      })
    },
    //获取文件类型
    getFileType(filename) {
      let typelist = ['pdf', 'txt', 'ppt', 'xlsx', 'doc', 'docx', 'pic', 'zip', 'txt', 'rar', 'mp3'];
      let piclist = ['png', 'gif', 'bmp', 'tif', 'tiff', 'jpe', 'jpeg', 'jpg'];
      let pdflist = ['pdf'];
      let pptlist = ['ppt', 'pptx'];
      let doclist = ['doc', 'docx'];
      let xlslist = ['xls', 'xlsx'];
      let ziplist = ['zip', 'rar'];
      let movlist = ['wmv', 'mp4'];
      let txtlist = ['txt', 'xml', 'html', 'css', 'js'];
      let mp3list = ['mp3', 'mp2', 'mpe', 'mpeg', 'mpg'];
      let typestring = '';
      let type = this.getFileTypeString(filename);
      if (piclist.indexOf(type) != -1) {
        typestring = 'pic';
      } else if (pptlist.indexOf(type) != -1) {
        typestring = 'ppt';
      } else if (pdflist.indexOf(type) != -1) {
        typestring = 'pdf';
      } else if (doclist.indexOf(type) != -1) {
        typestring = 'doc';
      } else if (xlslist.indexOf(type) != -1) {
        typestring = 'xls';
      } else if (txtlist.indexOf(type) != -1) {
        typestring = 'txt';
      } else if (mp3list.indexOf(type) != -1) {
        typestring = 'mp3';
      } else if (ziplist.indexOf(type) != -1) {
        typestring = 'rar';
      } else if (movlist.indexOf(type) != -1) {
        typestring = 'mov';
      } else {
        typestring = 'unno';
      }
      return typestring;
    },
    //截取文件后缀名
    getFileTypeString(filename) {
      filename = filename.toString();
      let type = filename.substring(filename.lastIndexOf(".") + 1, filename.length);
      return type;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
@import '../../style/folder.scss';
.content {
  background: #fff;
}

.foldermanage-content {
  border-top: none;
}

.breadbox {
  font-size: 14px;
  color: #3C98E8;
  font-weight: 500;
  line-height: 34px;
  margin-right: 10px;
  .breadicon {
    float: left;
    margin-right: 3px;
    .el-icon-d-arrow-left{
      color: #3C98E8;
    }
  }
}

</style>



